import React, { forwardRef } from "react";

import { ButtonBase } from "../Button/Button.types";
import { Icon } from "../Icon/Icon";
import { colors, Margins } from "../styleguide";

import {
	BadgeIconWrapper,
	ButtonText,
	IconTextWrapper,
	StyledAvatar,
	StyledMenuBarButton,
} from "./MenuBarButton.styles";


export interface MenuBarButtonProps extends Margins, ButtonBase {
	buttonText?: string
	avatarText?: string
	imageSource?: string
}

const MenuBarButton = forwardRef<HTMLButtonElement, MenuBarButtonProps>(({
	dataQA,
	buttonText,
	avatarText,
	imageSource,
	...props
}, ref) => (
	<StyledMenuBarButton
		ref={ref}
		data-qa={dataQA}
		{...props}
	>
		<IconTextWrapper>
			{(imageSource || avatarText) && (<StyledAvatar
				dataQA={`${dataQA}-avatar`}
				variant={imageSource ? "Image" : "Text"}
				size="Small"
				text={avatarText}
				imageSource={imageSource}
			/>)}
			{buttonText
			&& (
				<ButtonText
					dataQA="button-text"
					variant="bodyCopySmall"
					component="span"
					color={`${colors.black}A8`}
				>
					{buttonText}
				</ButtonText>
			)}
		</IconTextWrapper>
		<BadgeIconWrapper>
			<Icon
				role="presentation"
				color={`${colors.black}A8`}
				name="ChevronDown"
				size={16}
				strokeWidth={2}
			>
			</Icon>
		</BadgeIconWrapper>
	</StyledMenuBarButton>
));
MenuBarButton.displayName = "MenuBarButton";
export default MenuBarButton;
